import app from '../components/header';
import '../css/index.css'
import VueRouter from "vue-router";
Vue.use(VueRouter);
console.log(VueRouter);
new Vue({
    el:'app'
});



// 
var Foo = Vue.extend({
    template: '<p>This is foo!</p>'
})
var Bar = Vue.extend({
    template: '<p>This is bar!</p>'
})
// 
//  HTML 
var App = Vue.extend({})
// 
// 
var router = new VueRouter()
// 
//  Vue.extend
// 
// 
router.map({
    '/foo': {
        component: app
    },
    '/bar': {
        component: Bar
    }
})
// 
//  App  #app 
router.start(App, '#app')


